.UserDialog-Wrapper{ 
	position: fixed; top: 0; left: 0; bottom: 0; right: 0; 
	display: -ms-flexbox; 
	display: flex; -ms-flex-pack: center; justify-content: center; -ms-flex-align: center; align-items: center;
	/*background: hsla(0, 0%, 0%, 0.40)*/
	background-image: url('https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1504011428985&di=f9bba4f75e4fa9f633dadb5dac8e5424&imgtype=0&src=http%3A%2F%2Fpic.58pic.com%2F58pic%2F13%2F22%2F10%2F78J58PICqwN_1024.jpg');
}
.UserDialog{
	border: 1px solid;
	border-radius: 4px;
	min-width: 20em;
	/*height: 25em;*/
	background: white;
	border: 1px solid #aaa;
	-webkit-box-shadow: 0 0 1em hsla(0,0%,0%,0.35);
	        box-shadow: 0 0 1em hsla(0,0%,0%,0.35);
}
.UserDialog .signInOrSignUp > nav:first-child{ padding: 1em; margin-left: -2em; margin-right: -2em;}
.UserDialog .signInOrSignUp > nav:first-child label{ margin-left: 3em; cursor: pointer;}
.UserDialog .signInOrSignUp > .panes { padding: 0 1em 1em 2em; }
.UserDialog .signInOrSignUp .title h1{
	color: #f16e51;
	text-align: center;
}

.UserDialog .forgotPassword > h3 { padding: 1em; color: #f16e51; }
.UserDialog .forgotPassword > form{ padding: 0 1em 1em;  }
.UserDialog form>.row{ display: -ms-flexbox; display: flex; padding: .3em 0; }
.UserDialog form>.row>label:first-child{ width: 4em; }
.UserDialog form>.row.actions{ padding-left: 4em; display: -ms-flexbox; display: flex; -ms-flex-align: center; align-items: center; }
.UserDialog form>.row.actions button{margin-right: .5em;}
.UserDialog button{font: inherit; padding: .2em;}


