.TodoItem{
	padding: .5em;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-align: center;
	    align-items: center;
}
.TodoItem>.title{
	-ms-flex-positive: 1;
	    flex-grow: 1;
	padding: 0 .5em;
}
.TodoItem button{
	background: #f16e51;
	color: #fff;
	padding: 3px 8px;
	border: none;
}


